.input-container {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  gap: 8px;
  border-radius: var(--radius, 8px);
  border: 1px solid var(--gray-300, #d0d5dd);
  transition: all 0.3s ease-in-out;
  width: 100%;
  overflow: hidden;
  position: relative;
}
input {
  border: 0;
  border-radius: var(--radius, 8px);
  padding: 8px 14px;
  margin: 0;
  width: 100%;
  font-size: 16px;
  outline: none;
}
.placeholder {
  position: absolute;
  font-size: 16px;
  left: 14px;
  z-index: 2;
  transition: transform 0.3s ease-in-out;
  transform-origin: left top;
  pointer-events: none;
}
input:focus,
.input-container.has-value input {
  padding-block-start: 1.2rem;
  block-size: 40px;
}
input:focus + .placeholder,
.input-container.has-value .placeholder {
  transform: translateY(-10px) scale(0.7);
}
.input-container:focus-within {
  border-color: hsl(var(--brand-300, 206, 100%, 76%));
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px hsla(var(--brand-100, 209, 100%, 91%), 0.2);
}
.error {
  border-color: var(--error-300, #fda29b);
}
.disabled,
.disabled input {
  background: var(--gray-50, #f9fafb);
  color: var(--gray-300, #d0d5dd);
  pointer-events: none;
}

.input-container[data-context='icon'] input {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  padding-left: 0;
}
.placeholder {
  color: var(--gray-600);
}
label {
  padding-left: 14px;
}
