import { IExposedProperty, RoomType } from '@/models/property';
import { Component, Fragment, h, Listen, Prop } from '@stencil/core';
import { v4 } from 'uuid';

@Component({
  tag: 'ir-property-gallery',
  styleUrl: 'ir-property-gallery.css',
  shadow: true,
})
export class IrPropertyGallery {
  @Prop() exposed_property: IExposedProperty;
  @Prop() property_state: 'carousel' | 'gallery' = 'gallery';
  @Prop() roomType: RoomType;
  private irDialog: HTMLIrDialogElement;

  @Listen('openGallery')
  handleOpenGallery() {
    this.irDialog.openModal();
  }
  @Listen('carouselImageClicked')
  handleOpenCarouselGallery() {
    this.irDialog.openModal();
  }

  renderAmeneties() {
    const checkAmenity = (code: string) => {
      return this.exposed_property?.amenities.find(a => a.code === code);
    };
    const wifi = checkAmenity('freewifi');
    const climatecontrol = checkAmenity('climatecontrol');
    const balcony = checkAmenity('balcony');
    return (
      <ul class="flex items-center text-xs text-green-500 gap-2 flex-wrap">
        {wifi && (
          <li class="flex items-center gap-2">
            <ir-icons name="wifi" svgClassName="size-4"></ir-icons> <span>{wifi.description}</span>
          </li>
        )}
        {climatecontrol && (
          <li class="flex items-center gap-2">
            <ir-icons name="snowflake" svgClassName="size-4"></ir-icons> <span>{climatecontrol.description}</span>
          </li>
        )}
        {balcony && (
          <li class="flex items-center gap-2">
            <ir-icons name="sun" svgClassName="size-4"></ir-icons> <span>{balcony.description}</span>
          </li>
        )}
      </ul>
    );
  }
  render() {
    if (!this.exposed_property) {
      return null;
    }
    return (
      <div>
        {this.property_state === 'gallery' ? (
          <ir-gallery totalImages={this.exposed_property.images.length} images={this.exposed_property.images?.map(i => i.url).slice(0, 5)}></ir-gallery>
        ) : (
          <Fragment>
            <div class="flex text-gray-700 flex-wrap items-center gap-2 font-normal md:hidden text-sm py-2">
              {this.roomType?.bedding_setup.length > 0 && (
                <Fragment>
                  <ir-icons name="bed"></ir-icons>{' '}
                  {this.roomType?.bedding_setup?.map((bed_setup, index) => (
                    <p key={bed_setup.code}>
                      {bed_setup.name} {index < this.roomType.bedding_setup.length - 1 && <span> - </span>}
                    </p>
                  ))}
                </Fragment>
              )}
              {this.renderAmeneties()}
            </div>

            <div class="relative md:hidden block w-full h-48 carousel-container   rounded-md">
              <ir-carousel
                slides={this.roomType?.images?.map(img => ({
                  alt: img.tooltip,
                  id: v4(),
                  image_uri: img.url,
                }))}
              ></ir-carousel>
            </div>
            <div class="relative hidden md:block py-2">
              <div class="w-full md:w-auto md:max-h-[150px] mb-1 lg:max-h-[250px]  carousel-container   rounded-md">
                <ir-carousel
                  slides={this.roomType.images?.map(img => ({
                    alt: img.tooltip,
                    id: v4(),
                    image_uri: img.url,
                  }))}
                ></ir-carousel>
              </div>
              {this.roomType?.bedding_setup?.length > 0 && (
                <div class=" text-gray-700 items-center gap-2 font-normal hidden md:flex md:flex-wrap text-sm py-2">
                  <ir-icons name="bed"></ir-icons>{' '}
                  {this.roomType?.bedding_setup?.map((bed_setup, index) => (
                    <p key={bed_setup.code}>
                      {bed_setup.name} {index < this.roomType.bedding_setup.length - 1 && <span> - </span>}
                    </p>
                  ))}
                </div>
              )}
              {this.renderAmeneties()}
              <ir-button onButtonClick={() => this.irDialog.openModal()} variants="ghost" label="More details"></ir-button>
            </div>
          </Fragment>
        )}
        <ir-dialog ref={el => (this.irDialog = el)}>
          <div slot="modal-body" class="modal-container px-2 md:px-4 b-4 max-h-[80vh]">
            <div class=" bg-white z-50 h-14 flex justify-between items-center w-full">
              <h2 class="font-semibold text-lg md:text-xl">{this.property_state === 'carousel' ? this.roomType.name : this.exposed_property.name}</h2>
              <ir-button variants="icon" onButtonClick={() => this.irDialog.closeModal()}>
                <div slot="btn-icon">
                  <ir-icons name="xmark"></ir-icons>
                </div>
              </ir-button>
            </div>

            <div class="max-h-[70%] py-4">
              <ir-carousel
                slides={this.exposed_property.images.map(img => ({
                  alt: img.tooltip,
                  id: v4(),
                  image_uri: img.url,
                }))}
              ></ir-carousel>
            </div>
            {this.property_state === 'carousel' && (
              <section class={'text-sm mt-4'}>
                <h2 class="text-lg font-medium mb-5">Facilities and services</h2>
                <ir-facilities properties={this.exposed_property}></ir-facilities>
                <p innerHTML={this.exposed_property?.description.location_and_intro} class="py-2"></p>
              </section>
            )}
          </div>
        </ir-dialog>
      </div>
    );
  }
}
